<div class="forgot-password"><h2 class="mb-3">Forgot Password</h2><form class="row" @submit.prevent="onSubmit"><div class="col-md-6 col-lg-4 col-xl-3 mx-auto"><div class="mb-3"><input class="form-control" :class="{'is-invalid': !!error.validation.email}" v-model="email" type="email" name="email" placeholder="Email" required><div class="invalid-feedback text-start" v-if="error.validation.email"><template v-for="message in error.validation.email">{{ message }}<br></template></div></div><button class="btn btn-primary" :disabled="loading._" type="submit">Submit</button></div></form></div>